body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background-color: aliceblue;
}

.content-container {
    background-color: white;
    margin: 0px 50px;
    padding: 0px 20px;
    overflow-x: hidden;
}

.content-seperator {
    width: 98%;
}
.section-header {
    color: green;
}

.home-highlight-container {
    position: relative;
    text-align: center;
}
nav {
    background-color: white;
    height: 60px;
    top: 0px;
    box-shadow: 0 7px 6px -6px #999;

    position: relative;

    /* make navbar highest z-index to be the most front facing thing */
    z-index: 1;
}

nav .logo {
    position: relative;
    left: 20px;
    padding: 10px 25px;
    background-color: green;

    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: inline-block;
}

nav .logo a {
    position: relative;
    border: 2px solid;
    border-radius: 100%;
    padding: 5px;
    font-size: 18px;
    font-weight: 800;
    color: yellow;
    text-decoration: none; 
}

nav .search-bar {
    position: relative;
    left: 25%;
    width: 500px;
    height: 50%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-size: 16px;
}

nav .dropdown {
    position: absolute;
    display: inline-block;
    right: 20px;
    top: 35%;
    padding: 15px 50px;
    bottom:0px;
}

nav .dropdown-arrow {
    width: 0; 
    height: 0; 
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    
    border-top: 7px solid black;
}

nav .dropdown .dropdown-menu {
    position: absolute;

    width: 350px;
    background-color: lightslategrey;
    top: 40px;
    right: -20px;

    display: block;
    height: 0px;
    transition: height 500ms;

    overflow: hidden;
    opacity: 0.9;
    box-shadow: 0 7px 6px -6px #999;
}

nav .dropdown:hover .dropdown-menu {
    height: 275px;
}

nav .dropdown:hover .dropdown-arrow {
    width: 0; 
    height: 0; 
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    
    border-top: 7px solid green;
}

.dropdown-menu ul {
    position: relative;
    list-style: none;
    display: inline-block;
    vertical-align: top;
}

.dropdown-menu ul li {
    margin: 20px;
}

.dropdown-menu ul li a {
    text-decoration: none;
    color: yellow;
}

.dropdown-menu ul li a:hover {
    text-decoration: underline;
    color: green;
}

#mobile-nav {
    display: none;
}

@media only screen and (max-width: 940px) {
    #wide-nav {
        display: none;
    }

    #mobile-nav {
        display: block;
    }
}
.highlight-container {
    position: relative;
    height: 100px;
    width: 400px;
    border-radius: 10px;

    display: inline-block;
    margin-right: 10px;
}

.highlight-container > h5 {
    font-size: 1em;
    color: white;
    text-shadow: black 0px 0px 10px;
    text-align: center;
    margin: 5px 0px;
}

.highlight-performance-marker {
    border-radius: 10px;
    font-size: 15px;
    color: white;
    
    line-height: 15px;
    margin: 0px 0px;
    padding: 5px 5px;

    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 10px;
} 

.highlight-performance-increased {
    background-color: green;
}

.highlight-performance-decreased {
    background-color: red;
}

@media only screen and (max-width: 940px) {
    .highlight-container {
        margin-bottom: 10px;
        margin-right: 0px;
    }    

    .sector-highlights {
        text-align: center;
    }
}
.most-active-highlights-container {
    overflow: auto;
}

.most-active-highlights-table {
    overflow-y: scroll;
    display: table;
    border-spacing: 30px 0px;
}

.most-active-highlights-row {
    display: table-row;
}

.most-active-highlights-column {
    display: table-cell;
    background-color: floralwhite;
    position: relative;
    min-width: 150px;
}

.most-active-highlights-column-header {
    position: relative;
    padding-bottom: 100px;
}

.most-active-highlights-column-footer {
    position: absolute;
    bottom: 0px;
}


.center-div {
    position: absolute;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
}
